import React, { Component } from 'react'
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';
const mapStyles = {
    width: '80%',
    height: '70%',
    marginRight:'auto',
    marginLeft:'auto'
};
class GoogleMap extends Component {
    render() {
        return (
            <div>
                <Map
                    google={this.props.google}
                    zoom={8}
                    style={mapStyles}
                    initialCenter={{ lat: 35.1640982, lng: -117.9482658 }}
                >
                    <Marker position={{ lat: 35.1640982, lng: -117.9482658 }} />
                </Map>
            </div>
        )
    }
}
export default GoogleApiWrapper({
    apiKey: 'AIzaSyCaj0mHlR-keu-9hsR6d-gB0L9BclG04rk'
})(GoogleMap);
