import React, { Component } from 'react'
import Footer from './Footer'
import Nav from './Nav'
import './../css/contactUs.css';
import GoogleMap from './GoogleMap';
export default class ContactUs extends Component {

    render() {
        return (
            <div>
                <Nav />
                <div>
                    <div className="ContactUs-pageheader text-center">
                        <h1>Contact Us - Sanjalika Water Park</h1>
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item home">
                                <a href="#" title="Homepage">Homepage</a>
                            </li>
                            <li className="breadcrumb-item active">Contact Us</li>
                        </ul>
                    </div>
                    <div className="ContactUs-content">
                        <div className="container">
                            <div className="row">
                                <div className="col-md-6 left-content">
                                    <table className="table">
                                        <tbody>
                                            <tr>
                                                <td className="pr-3">Address: </td>
                                                <td>No 9 Boston Street, Los Angeles, California City, America</td>
                                            </tr>
                                            <tr>
                                                <td className="pr-3">Hotline:</td>
                                                <td>0989 200 699</td>
                                            </tr>
                                            <tr>
                                                <td className="pr-3">Email:</td>
                                                <td>Sanjalika@waterworld.com</td>
                                            </tr>
                                            <tr>
                                                <td className="pr-3">Follow us:</td>
                                                <td>
                                                    <ul style={{ display: 'flex', listStyle: 'none' }}>
                                                        <li className="mr-4" style={{ fontSize: 'x-large' }}><a href="#"><i className="fab fa-twitter" /></a></li>
                                                        <li className="mr-4" style={{ fontSize: 'x-large' }}><a href="#"><i className="fab fa-facebook" /></a></li>
                                                        <li className="mr-4" style={{ fontSize: 'x-large' }}><a href="#"><i className="fab fa-instagram" /></a></li>
                                                    </ul>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div className="col-md-6 right-content">
                                    <form action method="post" className="form" id="form-1">
                                        <div className="form-group">
                                            <label htmlFor="fullname" className="form-label">Full name <span style={{ color: 'red' }}>*</span></label>
                                            <input id="fullname" name="fullname" type="text" placeholder className="form-control" required />
                                            <span className="form-message" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="email" className="form-label">Email <span style={{ color: 'red' }}>*</span></label>
                                            <input id="email" name="email" type="email" placeholder className="form-control" required />
                                            <span className="form-message" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="Textarea">Messages here <span style={{ color: 'red' }}>*</span></label>
                                            <textarea className="form-control" id="textarea" rows={9} required defaultValue={""} />
                                        </div>
                                        <button className="submit btn btn-primary" type="submit">Send</button>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div style={{minHeight: '70vh', marginTop: '2%'}}>
                            <GoogleMap />
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}
